import React, { Component } from 'react';
import { InputText } from 'primereact/inputtext';
import { Redirect } from 'react-router-dom';
import { Dialog } from 'primereact/dialog';
import { Checkbox } from 'primereact/checkbox';
import { Growl } from 'primereact/growl';
import CurrentLocation from '../../../../components/currentLocation/currentLocation';
import { GoogleApiWrapper, InfoWindow, Marker } from 'google-maps-react';
import './login.css'
import ServiceProviderVm from '../../../../Models/Service-Provider/ServiceProviderVM';
import ServiceProviderService from '../../../../Services/Service-Provider-Api-Service/ServiceProviderService';
import ServiceProviderRequest from '../../../../Models/Service-Provider/ServiceProviderRequest';

export class Login extends Component {


  constructor(props) {
    super(props);
    this.state = {
      serviceProviderService: new ServiceProviderService,
      displayLogin: false,
      value2: "",
      serviceProvider: new ServiceProviderVm,
      showingInfoWindow: false,
      activeMarker: {},
      selectedPlace: {},
      firstname: ""
    };
    // console.log('google', this.props.google);
    this.signUpOnClick = this.signUpOnClick.bind(this);
    this.signUp = this.signUp.bind(this);
    this.onMarkerClick = this.onMarkerClick.bind(this);
    this.onClose = this.onClose.bind(this);
    this.showSuccess = this.showSuccess.bind(this);
    this.componentDidMount = this.componentDidMount.bind(this);

    const maps = this.props.google.maps;
    const { lat, lng } = {};
    new maps.LatLng(lat, lng);
    // console.log('locaton', this.state.serviceProvider);
  }

  Redirect() {
    return <Redirect to='/home' />;
  }

  signUp() {
    let serviceProviderRequest = new ServiceProviderRequest;
    serviceProviderRequest.serviceProviderVM = this.state.serviceProvider;
    
    let success = this.state.serviceProviderService.addNewServiceProvider(serviceProviderRequest);
    console.log("success", success);

    if (success) {
      this.setState({
        displayLogin: false,
        serviceProvider: new ServiceProviderVm
        
      });
      this.showSuccess("Successully signed up");
    }
    else
    {
      
    }

  }

  showSuccess(message) {
    this.growl.show({ severity: 'success', summary: 'Success', detail: message });
  }


  componentDidMount() {
    if (navigator && navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(pos => {
        const coords = pos.coords;

        this.state.serviceProvider.latitude = pos.coords.latitude;
        this.state.serviceProvider.longitude = pos.coords.longitude;

      });
    }
  }


  async signUpOnClick() {
    await this.setState({
      displayLogin: true
    });
    // console.log(this.state.displayLogin);

  }

  onMarkerClick = (props, marker, e) =>
    this.setState({
      selectedPlace: props,
      activeMarker: marker,
      showingInfoWindow: true
    });

  onClose = props => {
    if (this.state.showingInfoWindow) {
      this.setState({
        showingInfoWindow: false,
        activeMarker: null
      });
    }
  };

  valueChanges(val, id) {
    if (id == 1) {
      this.state.serviceProvider.firstName = val;
    }
    else if (id == 2) {
      this.state.serviceProvider.lastName = val;
    }
    else if (id == 3) {
      this.state.serviceProvider.companyName = val;
    }
    else if (id == 4) {
      this.state.serviceProvider.companyAddrss = val;
    }
    else if (id == 5) {
      this.state.serviceProvider.contactNo = val;
    }

  }
  render() {
    return (

      <div className="component-login" style={{ padding: 0 }}>
        <Growl ref={(el) => this.growl = el} />
        <div className="container-fluid1 background" style={{ padding: 0 }}>
          <div className="background-top col-md-12">

          </div>

          <div className="background-bottom col-md-12">

          </div>
        </div>

        <div className="midLine row">
          <div className="col-md-12" style={{ padding: 0, margin: 0, marginTop: 2 }}>
            <div className="col-md-12 text-center">
              <div className="card">
                <div>
                  <label className="connectWith">Connect With</label>
                  <img className="middleLogo" src={require('../../../../assets/logo.png')} alt="logo" />
                </div>

                <div className="container-fluid">

                  <div style={{ marginTop: '2.5rem', marginBottom: '2.5rem' }}>
                    <div className="field">
                      <InputText placeholder="Username/E-mail" type="text" size="40" />
                    </div>
                    <div className="field">
                      <InputText placeholder="Password" type="password" size="40" />
                    </div>

                    <div>
                      <button className="btn btn-primary" onClick={() => this.Redirect}>Login</button>
                    </div>
                    <div className="text-center">
                      <label className="isOwner" onClick={this.signUpOnClick}>Are you a restaurant owner?</label>
                    </div>
                  </div>

                  <div >
                    <button className="loginBtn loginBtn--facebook">
                      Login with Facebook</button>

                    <button className="loginBtn loginBtn--google">
                      Login with Google</button>
                  </div>

                </div>

              </div>
            </div>
          </div>
        </div>
        <div >
          <Dialog header="Sign Up" visible={this.state.displayLogin} sc baseZIndex={5000000} style={{ width: '50vw', maxHeight: '40vw' }} maximizable={true} modal={true} onHide={(e) => this.setState({ displayLogin: false })}>
            <div className="row  dialogDiv">
              <div className="col-md-12" style={{ backgroundColor: '#FFAF1A' }}>
                <label style={{ fontWeight: 'bold', color: 'white' }}>Fill up all the required fields</label>
              </div>

              <div className="col-md-12 row">
                <div className="col-md-6 formField">
                  <InputText id="float-input" type="text" size="30" placeholder={"First Name"} onChange={(e) => this.valueChanges(e.target.value, 1)} />

                </div>
                <div className="col-md-6 formField">
                  <InputText id="float-input1" type="text" size="30" placeholder={"Last Name"} onChange={(e) => this.valueChanges(e.target.value, 2)} />

                </div>
                <div className="col-md-6 formField">

                  <InputText id="float-input2" type="text" size="30" placeholder={"Company Name"} onChange={(e) => this.valueChanges(e.target.value, 3)} />

                </div>
                <div className="col-md-6 formField">

                  <InputText id="float-input3" type="text" size="30" placeholder={"Company Address"} onChange={(e) => this.valueChanges(e.target.value, 4)} />

                </div>

                <div className="col-md-6 formField">

                  <InputText id="float-input4" type="text" size="30" placeholder={"Contact No"} onChange={(e) => this.valueChanges(e.target.value, 5)} />

                </div>
                <div className="col-md-6">
                  {/* <Checkbox inputId="cb2" value="Save current location as restaurant location." /> */}
                  {/* onChange={this.onCityChange} checked={this.state.cities.indexOf('San Francisco') !== -1} */}
                </div>

                <div className="col-md-12" style={{ height: '30vh', width: '30%' }}>

                  <CurrentLocation
                    centerAroundCurrentLocation
                    google={this.props.google}
                  >
                    <Marker onClick={this.onMarkerClick} name={'current location'} />
                    <InfoWindow
                      marker={this.state.activeMarker}
                      visible={this.state.showingInfoWindow}
                      onClose={this.onClose}
                    >
                      <div>
                        <h4>{this.state.selectedPlace.name}</h4>
                      </div>
                    </InfoWindow>
                  </CurrentLocation>
                </div>
                <div className="col-md-12" style={{ marginTop: '1rem' }}>
                  <div className="p-col-12">
                    <Checkbox inputId="cb1" value="New York" ></Checkbox>
                    {/* onChange={this.onCityChange} checked={this.state.cities.indexOf('New York') !== -1}> */}
                    <label htmlFor="cb1" className="p-checkbox-label">Save current location as restaurant location.</label>
                  </div>
                </div>
              </div>
              <div className="col-md-12" style={{ textAlign: 'right', marginTop: '1rem' }}>
                <span className="col-md-12" >
                  <button className="btn btn-warning btnn" onClick={this.signUp}>Sign me up</button>
                  <button className="btn btn-secondary btnn">Clear</button>
                  <button className="btn btn-danger btnn">Cancel</button>
                </span>
              </div>
            </div>


          </Dialog>
        </div>

      </div>

    );
  }
}
export default GoogleApiWrapper({
  apiKey: 'AIzaSyAU6UmJAucytowydAjnG7OE8LcbDi8eGoA'
})(Login);