export default class SideMenu extends Component {

  navigateToPage = (path) => {

    if (this.state.redirect) {
      // withRouter(({ history }) => {
      //   history.push(path);
      // })
      window.location.replace(path);
    }

  }

  async loadNavigator(path) {
    await this.setState({
      path: path
    });
     await this.setState({
      redirect: true
    });
  }

  constructor() {
    super();
    
    this.state = {
      path:"",
      redirect: false,
      items: [
        {
          label: 'Dashboard',
          icon: 'fas fa-chart-line',
          items: null,
          command: () => { 
            this.loadNavigator('/Dashboard');
          }
        },
        {
          label: 'Food',
          icon: 'fas fa-utensils',
          items: [
            {
              label: 'View Food',
              icon: 'pi pi-fw pi-align-left',
              command: () => { 
                this.loadNavigator('/ViewFoodItems');
              }
            },
            {
              label: 'Add Food',
              icon: 'pi pi-fw pi-align-right',
              command: () => { 
                this.loadNavigator('/AddMeals');
              }
            }
          ]
        },
        {
          label: 'Events',
          icon: 'fas fa-calendar',
          items: [
            {
              label: 'Edit',
              icon: 'pi pi-fw pi-pencil',
              items: [
                {
                  label: 'Save',
                  icon: 'pi pi-fw pi-calendar-plus'
                },
                {
                  label: 'Delete',
                  icon: 'pi pi-fw pi-calendar-minus'
                },

              ]
            },
            {
              label: 'Archieve',
              icon: 'pi pi-fw pi-calendar-times',
              items: [
                {
                  label: 'Remove',
                  icon: 'pi pi-fw pi-calendar-minus'
                }
              ]
            }
          ]
        },
        {
          label: 'Profile',
          icon: 'fas fa-user',
          items: [
            {
              label: 'Restaurant',
              icon: 'pi pi-fw pi-pencil',
              items:null
            },
            {
              label: 'Owner',
              icon: 'pi pi-fw pi-calendar-times',
              items: null
            }
          ]
        },
        {
          separator: false
        },
        {
          label: 'Quit',
          icon: 'pi pi-fw pi-power-off',
          command: () => { 
            this.loadNavigator('/Login');
          }
        }
      ]
    }
  }
  render() {
    return (
      <div className="side-menu">
        {this.navigateToPage(this.state.path)}
        <SlideMenu model={this.state.items} className="sideMenu" viewportHeight={520}></SlideMenu>
      </div>
    );
  }
}
