import axios from 'axios';
import ServiceProviderRequest from '../../Models/Service-Provider/ServiceProviderRequest';
import ServiceProviderResponse from '../../Models/Service-Provider/ServiceProviderResponse';

export default class ServiceProviderService {

    async addNewServiceProvider(serviceProviderRequest = new ServiceProviderRequest) {
        let returning=false;
        await axios.post('http://localhost:8080/addServiceProvider', serviceProviderRequest, {
            headers: {
                'Access-Control-Allow-Origin': '*'
            },
            proxy: {
                host: '104.236.174.88',
                port: 3128
            }
        }).then(response => {
            console.log(response.data.isSuccess);
            returning= response.data.isSuccess;
        })
        return returning
    }

    getAllServiceProviders() {

        axios.get('http://localhost:8080/getAllServiceProviders', {
            headers: {
                'Access-Control-Allow-Origin': '*'
            },
            proxy: {
                host: '104.236.174.88',
                port: 3128
            }
        }).then(response => {
            console.log(response);
        })

    }
}