export default class AddMeals extends Component {
  constructor(props) {
    super(props);
    this.state = {
      meal : new MealVM,
      brand: "",
      formData: new FormData,
      brandSuggestions: {},
      selectedIngredient: {},
      selectedPortion: {},
      ingredients: [] = [{ name: "Chicken", id: 1 },
      { name: "Fish", id: 2 },
      { name: "Carrot", id: 3 }],
      portions: [] = [{ name: "Small", id: 1 },
      { name: "Large", id: 2 },
      { name: "Full", id: 3 },
      { name: "Normal", id: 3 },
      ],

    };

    this.setState = ({
      ingredients: [{ name: "Chicken", id: 1 },
      { name: "Fish", id: 2 },
      { name: "Carrot", id: 3 }],
    })
  }
  ingredientOnChange = () => {
    this.state.ingredients.push(this.state.selectedIngredient);
  }

  loadSelectedIngredients() {

  }

  suggestBrands() {

  }

  valueChanges(val, id) {
    if (id == 1) {
      this.state.meal.category = val;
    }
    else if (id == 2) {
      this.state.meal.foodName = val;
    }
    else if (id == 3) {
      this.state.meal.price = val;
    }
    else if (id == 4) {
      console.log(val);
      for (let file of val.files) {
        this.state.formData.append('eee', file);
        this.state.meal.image.push(file);
      }
    }
    else if (id == 5) {
      this.state.meal.contactNo = val;
    }

  }

  render() {
    return (

      <Fieldset legend="Add Meals">
        <div className="row">
          <div className="col-md-12 margin">
            <span className="p-float-label">
              <InputText id="in" size="24" value={this.state.value} onChange={(e) => this.setState({ value: e.target.value })} />
              <label htmlFor="in">Select a category</label>
            </span>
          </div>

          <div className="col-md-4 field margin">
            <div className="p-inputgroup">
              <span className="p-inputgroup-addon">
                <i className="fas fa-hamburger"></i>
              </span>
              <span className="p-float-label">

                <InputText id="f2" />
                <label htmlFor="f2">Meal Name</label>
              </span>

            </div>
          </div>

          <div className="col-md-7 field margin">
            <div className="p-inputgroup">
              <span className="p-inputgroup-addon">
                <i className="fas fa-dollar-sign"></i>
              </span>
              <span className="p-float-label">

                <InputText id="f2" />
                <label htmlFor="f2">Price</label>
              </span>
            </div>
          </div>

          <div className="row col-md-12 margin" style={{ padding: 0 }}>
            <div className="col-md-12 ">
              <Dropdown filter={true} className="field" value={this.state.selectedIngredient} options={this.state.ingredients} onChange={this.ingredientOnChange} placeholder="Add Ingredients" optionLabel="name" />
            </div>
            <div className="col-md-12">

              <div className="item" >
                <div className="tags">
                  <span>
                    Chicken
                      <i className="fas fa-times" title="remove"></i>
                  </span>

                  <span>
                    Cheese
                      <i className="fas fa-times" title="remove"></i>
                  </span>

                  <span>
                    Carrot
                      <i className="fas fa-times" title="remove"></i>
                  </span>
                </div>
              </div>
            </div>

          </div>

          <div className="row col-md-12 margin"  >
            <div className="col-md-12 " style={{ padding: 0 }}>
              <Dropdown filter={true} className="field" value={this.state.selectedIngredient} options={this.state.portions} onChange={this.potionOnChange} placeholder="Add Quantities" optionLabel="name" />
            </div>
            <div className="col-md-12" style={{ padding: 0 }}>

              <div className="item" >
                <div className="tags">
                  <span>
                    Small
                      <i className="fas fa-times" title="remove"></i>
                  </span>

                  <span>
                    Large
                      <i className="fas fa-times" title="remove"></i>
                  </span>

                </div>
              </div>
            </div>

          </div>

          <div className="col-md-11  margin " >
            <div className="col-md-7" style={{ padding: 0}}>
              <FileUpload name="demo[]" url="./upload.php" onSelect={(event) => this.valueChanges(event,4)}
                multiple={true} accept="image/*" maxFileSize={3000000} />
            </div>
          </div>

        </div >
      </Fieldset>


    );
  }
}


