import React, { Component } from 'react';
import './App.css';
import Routes from '../src/Routing/Routes'
import { library } from '@fortawesome/fontawesome-svg-core'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faStroopwafel } from '@fortawesome/free-solid-svg-icons'
import TopNavBar from './Common-components/src/components/topNavBar/topNavBar';
import Home from './components/home/home';
import SideMenu from './Common-components/src/components/sideMenu/SideMenu';
import * as Stomp from 'stompjs';
import * as SockJS from 'sockjs-client';

library.add(faStroopwafel);
class App extends Component {

  constructor(props) {
    super(props);
    console.log(this.props);


  }


    componentDidMount() {
      this.initializeWebSocketConnection();
  }

  initializeWebSocketConnection(){
    let ws = new SockJS('http://localhost:8080/websocket-notification');
    this.stompClient = Stomp.over(ws);
    let that = this;
    this.stompClient.connect({}, function(frame) {
      that.stompClient.subscribe("/notif", (message) => {
        if(message.body) {
          console.log('message',JSON.parse(message.body).notificationVM);
        }
      });
    });
  }

  click() {
    console.log("clicked");
  }

  render() {
    return (<div className="container-fluid">
      <div className="row" style={{ position: 'relative' }}>

        <div className="col-md-11" style={{ padding: 'auto', zIndex: 5000 }}>
          <Home />
        </div>
      </div>
      <div className="row" style={{ padding: '0px', position: 'relative', marginTop: '8%' }}>

        <div className="col-md-2 side-menu" style={{ position: 'realtive', padding: 0 }}>
          <div style={{ position: 'fixed', padding: 0 }}>
            <SideMenu />
          </div>
        </div>
        <div className="col-md-10" style={{ position: 'relative' }}>
          <Routes />
        </div>
      </div>

      {/* <Routes  /> */}
      {/* <button onClick={this.click}>asdad</button> */}
    </div>
    );
  }
}

export default App;
